import type { EventName } from "@lit-labs/react";
import * as WebComponentModule from "@vaadin/message-input/vaadin-message-input.js";
import * as React from "react";
import { createComponent, WebComponentProps } from "../utils/createComponent.js";
export type MessageInputEventMap = Readonly<{
    onSubmit: EventName<WebComponentModule.MessageInputEventMap["submit"]>;
}>;
const events = { onSubmit: "submit" } as MessageInputEventMap;
export type MessageInputProps = WebComponentProps<WebComponentModule.MessageInput, MessageInputEventMap>;
export const MessageInput = createComponent({
    elementClass: WebComponentModule.MessageInput,
    events,
    react: React,
    tagName: "vaadin-message-input"
});
export { WebComponentModule };
